export default function PgzlistCtrl($http) {
  var vm = this;
  vm.list=[
     {name:'', key:'title', width:'30%',link:'http://zc.dreamhiway.com/cpzcDetail/p', linkkey:'fpid'},
     {name:'', key:'name', width:'20%'},
     {name:'', key:'status', width:'20%'},
     {
      name: '', key: [
        {zid:' :',
         zikey:'moneys'
        },
        { zid:' :',
          zikey:'progress',
          zikeyfh:'%'
        },
       ],width:'20%',
     },
   
  ]
  
  //--
 vm.cancel = function(cancelId) {
   $http.post('/CpzcGzList/DelAttention',{id:cancelId}).success( res => {
      if(res.success) {
        location.reload();
      }
   })
 }
  
  
}


