export default function s_AllCtrl ($scope) {
  var vm = this;
  vm.list = [
    { name: '', key: 'projectname', width: '20%'},
    { name: '', key: 'receiveName', width: '15%'},
    { name: '', key: 'hbtitle', width: '15%'},
    { name: '', key: 'number', width: '20%'},
    { name: '', key: 'hbcount', width: '10%'},
    { name: '', key: 'cost', width: '10%'},
    { name: '', key: 'statename', width: '10%'},
  ]
 vm.id = $scope.$parent.sellerVm.id
}


